import React, { useState, useEffect } from "react";
import "./AssignStaff.css";
import { DashboardHeader } from "../..";
import { Form, Select, Button, Row, Col, Modal } from "antd";
import { db } from "../../../../firebase";

const AssignStaff = (props) => {
  const [isModalVisible, setIsModalVisible] = useState(false);
  const [job, setJob] = useState("");
  const [staff, setStaff] = useState("");
  const [jobsList, setJobsList] = useState([]);
  const [employeesList, setEmployeesList] = useState([]);

  const { Option } = Select;

  useEffect(() => {
    getJobs();
    getEmployees();
  }, []);

  const getJobs = () => {
    db.collection("jobs")
      .get()
      .then((querSnapshot) => {
        let datas = [];
        querSnapshot.forEach((doc) => {
          const data = { jobID: doc.id, jobTitle: doc.data().jobTitle };
          datas = [...datas, data];
        });
        setJobsList(datas);
      });
  };

  const getEmployees = () => {
    db.collection("users")
      .where("role", "==", "employee")
      .get()
      .then((querySnapshot) => {
        let datas = [];
        querySnapshot.forEach((doc) => {
          const data = { employeeID: doc.id, employeeName: doc.data().name };
          datas = [...datas, data];
        });
        setEmployeesList(datas);
      });
  };

  const handleOk = () => {
    setIsModalVisible(false);
  };

  const handleCancel = () => {
    setIsModalVisible(false);
  };

  const onFinish = (values) => {
    setJob(values.job);
    setStaff(values.staff);
    setIsModalVisible(true);
    console.log(values);
  };

  return (
    <>
      <DashboardHeader
        header="Assign Staff"
        goBack={props.history.goBack}
        isNested
      />
      <div className="assignStaff">
        <Form onFinish={onFinish}>
          <Row gutter={[16, 0]}>
            <Col xs={24} sm={12}>
              <Form.Item
                name="job"
                rules={[{ required: true, message: "Input required!" }]}
              >
                <Select
                  size="large"
                  style={{ fontSize: "14px" }}
                  showSearch
                  placeholder="Select Job"
                  optionFilterProp="children"
                  filterOption={(input, option) =>
                    option.children
                      .toLowerCase()
                      .indexOf(input.toLowerCase()) >= 0
                  }
                >
                  {jobsList.map(({ jobTitle }, index) => (
                    <Option key={index} value={jobTitle}>
                      {jobTitle}
                    </Option>
                  ))}
                </Select>
              </Form.Item>
            </Col>

            <Col xs={24} sm={12}>
              <Form.Item
                name="staff"
                rules={[{ required: true, message: "Input required!" }]}
              >
                <Select
                  size="large"
                  style={{ fontSize: "14px" }}
                  showSearch
                  placeholder="Select Staff"
                  optionFilterProp="children"
                  filterOption={(input, option) =>
                    option.children
                      .toLowerCase()
                      .indexOf(input.toLowerCase()) >= 0
                  }
                >
                  {employeesList.map(({ employeeName }, index) => (
                    <Option key={index} value={employeeName}>
                      {employeeName}
                    </Option>
                  ))}
                </Select>
              </Form.Item>
            </Col>
          </Row>
          <div className="assignStaff__assignBtn">
            <Button className="bg-dark" htmlType="submit">
              ASSIGN
            </Button>
          </div>
        </Form>
      </div>
      <Modal
        title="Basic Modal"
        visible={isModalVisible}
        onOk={handleOk}
        onCancel={handleCancel}
        footer={[]}
      >
        <div className="assignStaff__modal">
          <p>
            Job: <span>{job}</span>
          </p>
          <p>
            Assigned to: <span>{staff}</span>
          </p>
        </div>
      </Modal>
    </>
  );
};

export default AssignStaff;
