import React from 'react'
import { DashboardHeader } from '../../../DashboardHeader'
import { InvoiceHistoryItem } from '../InvoiceHistoryItem';
import './ViewInvoiceLanding.css';


const ViewInvoiceLanding = (props) => {
    const ITEMS = [
        {
            header:"Client Invoice History", slug:"client-invoice-history"
        },
        {
            header:" Staff Invoice History", slug:"staff-invoice-history"
        },
    ]
    return (
        <div>
            <DashboardHeader
            header="View Invoice History"
            goBack={props.history.goBack}
            isNested/>
            <div className="invoice_container">
                <ul>
                    {
                        ITEMS.map(({header,slug},index) =>(
                            <InvoiceHistoryItem key={index}  header={header} slug={`view-invoice-history/${slug}`} />
                        ))
                    }

                </ul>
            </div>
            
        </div>
    )
}

export default ViewInvoiceLanding
