import React from "react";
import { Button, Input, message, Form } from "antd";
import "./AddClient.css";
import { DashboardHeader } from "../..";
import firebase, { db } from "./../../../../firebase";

const AddClient = (props) => {
  const [form] = Form.useForm();

  const onFinish = (values, clearProps) => {
    console.log(values);
    let date = firebase.firestore.Timestamp.now();
    let ClientEmail = {
      uploadedAt: date,
      email: values.email,
    };

    let accountEmail = {
      uploadedAt: date,
      email: values.email,
      status:'active',
    };

    db.collection("Client Email")
      .doc()
      .set(ClientEmail)
      .then(() => {
        message.success("client email submitted successfully");
      })
      .catch((error) => message.error(error));

      db.collection("Account")
      .doc()
      .set(accountEmail)
      .then(() => {
        // message.success("client email submitted successfully");
      })
      .catch((error) => message.error(error));

    form.resetFields();
  };

  return (
    <>
      <DashboardHeader
        header={"Add Clients"}
        goBack={props.history.goBack}
        isNested
      />
      <div className="add_user_container">
        <h3>Email Address*</h3>

        <Form
          name="normal_login"
          className="login-form"
          onFinish={onFinish}
          form={form}
        >
          <Form.Item
            name="email"
            rules={[
              {
                required: true,
                type: "email",
                message: "Please input your email address!",
              },
            ]}
          >
            <Input />
          </Form.Item>

          <Form.Item>
            <Button className="bg-dark" htmlType="submit">
              Register Clients
            </Button>
          </Form.Item>
        </Form>
      </div>
    </>
  );
};

export default AddClient;
