import React, { useState, useEffect } from "react";
import "./NewSchedule.css";
import { DashboardHeader } from "../../..";
import { Modal, Button, Form, Select, Input, Table } from "antd";
import { db } from "../../../../../firebase";

const NewSchedule = (props) => {
  const [isModalVisible, setIsModalVisible] = useState(true);
  const [step, setStep] = useState(1);
  const [isDaysDisabled, setIsDaysDisabled] = useState(true);
  const [isDateDisabled, setIsDateDisabled] = useState(true);
  const [taskCount, setTaskCount] = useState(1);
  const [tasks, setTasks] = useState([]);
  const [clientsList, setClientsList] = useState([]);
  const [jobsList, setJobsList] = useState([]);
  const [isTasksDone, setIsTasksDone] = useState(false);
  const [client, setClient] = useState({});
  const [job, setJob] = useState({});

  useEffect(() => {
    getClients();
  }, []);

  const { Option } = Select;
  const [form] = Form.useForm();

  const handleCancel = () => {
    setIsModalVisible(false);
    props.history.goBack();
  };

  const handleChange = (value) => {
    if (
      value === "Daily" ||
      value === "Business Days" ||
      value === "Weekends"
    ) {
      setIsDaysDisabled(true);
      setIsDateDisabled(true);
    } else if (value === "Monthly") {
      setIsDaysDisabled(true);
      setIsDateDisabled(false);
    } else {
      setIsDaysDisabled(false);
      setIsDateDisabled(true);
    }
  };

  const onFinish = (values) => {
    const { taskName, frequency, days, date } = values;

    step === 1 && getJobs(client);

    if (step < 3) {
      setStep(step + 1);
    } else {
      setTasks([
        ...tasks,
        {
          taskName: taskName
            .toLowerCase()
            .split(" ")
            .map((word) => word.charAt(0).toUpperCase() + word.substring(1))
            .join(" "),
          frequency,
          days: isDaysDisabled ? null : days,
          date: isDateDisabled ? null : date,
        },
      ]);

      if (!isTasksDone) {
        form.resetFields();
        setIsDaysDisabled(true);
        setIsDateDisabled(true);
        setTaskCount(taskCount + 1);
      } else {
        setStep(step + 1);

        tasks.map((task) =>
          db
            .collection("schedules")
            .doc()
            .set({
              ...client,
              ...job,
              ...task,
            })
        );

        db.collection("schedules")
          .doc()
          .set({
            ...client,
            ...job,
            taskName: taskName
              .toLowerCase()
              .split(" ")
              .map((word) => word.charAt(0).toUpperCase() + word.substring(1))
              .join(" "),
            frequency,
            days: isDaysDisabled ? null : days,
            date: isDateDisabled ? null : date,
          });
      }
    }
  };

  const getClients = () => {
    db.collection("users")
      .where("role", "==", "client")
      .get()
      .then((querySnapshot) => {
        let datas = [];
        querySnapshot.forEach((doc) => {
          const data = { ...doc.data(), id: doc.id };
          datas = [...datas, data];
        });
        setClientsList(datas);
      });
  };

  const getJobs = ({ clientID }) => {
    db.collection("jobs")
      .where("clientID", "==", clientID)
      .get()
      .then((querySnapshot) => {
        let datas = [];
        querySnapshot.forEach((doc) => {
          const data = { ...doc.data(), id: doc.id };
          datas = [...datas, data];
        });
        setJobsList(datas);
      });
  };

  const switchForm = () => {
    switch (step) {
      case 1:
        return (
          <Form.Item
            label="Client"
            name="client"
            rules={[{ required: true, message: "Required!" }]}
          >
            <Select
              size="large"
              style={{ fontSize: "14px", width: "100%" }}
              onChange={(value, { key }) =>
                setClient({ clientID: key, clientName: value })
              }
              showSearch
              optionFilterProp="children"
              filterOption={(input, option) =>
                option.children.toLowerCase().indexOf(input.toLowerCase()) >= 0
              }
            >
              {clientsList.map(({ id, name, email }) => (
                <Option key={id} value={name}>
                  <p className="newSchedule__dropOption">
                    <span>{name}</span>
                    <span>{email}</span>
                  </p>
                </Option>
              ))}
            </Select>
          </Form.Item>
        );

      case 2:
        return (
          <>
            <Form.Item
              label="Job"
              name="jobTitle"
              rules={[{ required: true, message: "Required!" }]}
            >
              <Select
                size="large"
                style={{ fontSize: "14px", width: "100%" }}
                onChange={(value, { key }) =>
                  setJob({ jobID: key, jobTitle: value })
                }
                showSearch
                optionFilterProp="children"
                filterOption={(input, option) =>
                  option.children.toLowerCase().indexOf(input.toLowerCase()) >=
                  0
                }
              >
                {jobsList.map(({ id, jobTitle }) => (
                  <Option key={id} value={jobTitle}>
                    {jobTitle}
                  </Option>
                ))}
              </Select>
            </Form.Item>
          </>
        );

      case 3:
        const getDates = () => {
          const options = [];
          for (let i = 1; i < 31; i++) options.push(i);
          return (
            <Select
              size="large"
              style={{ width: "90px" }}
              disabled={isDateDisabled}
            >
              {options.map((option) => (
                <Option
                  key={option}
                  value={
                    option +
                    (option > 0
                      ? ["th", "st", "nd", "rd"][
                          (option > 3 && option < 21) || option % 10 > 3
                            ? 0
                            : option % 10
                        ]
                      : "")
                  }
                >
                  {option +
                    (option > 0
                      ? ["th", "st", "nd", "rd"][
                          (option > 3 && option < 21) || option % 10 > 3
                            ? 0
                            : option % 10
                        ]
                      : "")}
                </Option>
              ))}
            </Select>
          );
        };

        return (
          <>
            <p style={{ textAlign: "center", paddingBottom: "1rem" }}>
              Task: {taskCount}
            </p>
            <Form.Item
              label="Task Name"
              name="taskName"
              rules={[{ required: true, message: "Required!" }]}
            >
              <Input size="large" style={{ fontSize: "14px" }} />
            </Form.Item>

            <Form.Item
              label="Frequency"
              name="frequency"
              rules={[{ required: true, message: "Required!" }]}
            >
              <Select
                size="large"
                style={{ fontSize: "14px", width: "100%" }}
                onChange={(value) => handleChange(value)}
              >
                <Option value="Daily">Daily (All 7 Days)</Option>
                <Option value="Business Days">Business Days (Mon - Fri)</Option>
                <Option value="Weekends">Weekends (Sat - Sun)</Option>
                <Option value="Fortnightly">Fortnightly</Option>
                <Option value="Monthly">Monthly</Option>
                <Option value="Weekly">Weekly</Option>
              </Select>
            </Form.Item>

            <Form.Item
              label="Day/s"
              name="days"
              rules={
                !isDaysDisabled && [{ required: true, message: "Required!" }]
              }
            >
              <Select
                mode="multiple"
                size="large"
                style={{ width: "100%" }}
                disabled={isDaysDisabled}
              >
                <Option value="Sunday">Sunday</Option>
                <Option value="Monday">Monday</Option>
                <Option value="Tuesday">Tuesday</Option>
                <Option value="Wednesday">Wednesday</Option>
                <Option value="Thursday">Thursday</Option>
                <Option value="Friday">Friday</Option>
                <Option value="Saturday">Saturday</Option>
              </Select>
            </Form.Item>

            <Form.Item
              label="Date"
              name="date"
              rules={
                !isDateDisabled && [{ required: true, message: "Required!" }]
              }
            >
              {/* <DatePicker
                picker="date"
                onChange={(value) => setDate(moment(value).format("LL"))}
                disabled={isDateDisabled}
                placeholder=""
                style={{ width: "60%", height: "2.4rem" }}
              /> */}
              {getDates()}
            </Form.Item>
          </>
        );

      case 4:
        const COLUMNS = [
          {
            title: "Name",
            dataIndex: "taskName",
            key: "taskName",
          },
          {
            title: "Frequency",
            dataIndex: "frequency",
            key: "frequency",
          },
          {
            title: "Appointment/s",
            dataIndex: "appointment",
            key: "appointment",
          },
        ];

        const datas = tasks.map(
          ({ taskName, frequency, days, date }, index) => ({
            key: index,
            taskName,
            frequency,
            appointment: days ? days.join(", ") : date ? date : "",
          })
        );

        return (
          <div className="newSchedule__formValues">
            <p>
              Client: <span>{client.clientName}</span>
            </p>
            <p>
              Job: <span>{job.jobTitle}</span>
            </p>
            <p>Task/s: </p>
            <div className="newSchedule__table">
              <Table columns={COLUMNS} dataSource={datas} pagination={false} />
            </div>
          </div>
        );

      default:
        break;
    }
  };

  return (
    <>
      <DashboardHeader
        header="New Schedule"
        goBack={props.history.goBack}
        isNested
      />
      <Modal
        className="newSchedule"
        title="New Schedule"
        visible={isModalVisible}
        onCancel={handleCancel}
        maskClosable={false}
        footer={[
          <Button
            className={step === 3 ? "bg-light" : "newSchedule__btnHide"}
            key="addNew"
            htmlType="submit"
            form="newScheduleForm"
          >
            ADD NEW
          </Button>,

          <Button
            className={step < 3 ? "bg-dark" : "newSchedule__btnHide"}
            key="continue"
            htmlType="submit"
            form="newScheduleForm"
          >
            CONTINUE
          </Button>,
          <Button
            className={step === 3 ? "bg-dark" : "newSchedule__btnHide"}
            key="create"
            form="newScheduleForm"
            htmlType="submit"
            onClick={() => setIsTasksDone(true)}
          >
            CREATE
          </Button>,
        ]}
      >
        <div className="newSchedule__form">
          <Form id="newScheduleForm" onFinish={onFinish} form={form}>
            {switchForm()}
          </Form>
        </div>
      </Modal>
    </>
  );
};

export default NewSchedule;
