import React from "react";
import "./ManageJobs.css";
import { Switch, Route } from "react-router-dom";
import {
  ManageJobsLanding,
  JobBasis,
  AssignStaff,
  EmployeeBasis,
  ViewDetails,
  ViewEmployeeDetails,
} from ".";

const ManageJobs = () => {
  return (
    <>
      <Switch>
        <Route
          path="/dashboard/manage-jobs"
          component={ManageJobsLanding}
          exact
        />
        <Route
          path="/dashboard/manage-jobs/job-basis"
          component={JobBasis}
          exact
        />
        <Route
          path="/dashboard/manage-jobs/job-basis/assign-staff"
          component={AssignStaff}
          exact
        />
        <Route
          path="/dashboard/manage-jobs/job-basis/:id"
          component={ViewDetails}
          exact
        />
        <Route
          path="/dashboard/manage-jobs/employee-basis"
          component={EmployeeBasis}
          exact
        />
        <Route
          path="/dashboard/manage-jobs/employee-basis/:id"
          component={ViewEmployeeDetails}
          exact
        />
      </Switch>
    </>
  );
};

export default ManageJobs;
