import React from "react";
import { Route, Switch } from "react-router-dom";
import "./HistoryManagement.css";
import { HistoryManagementLanding } from "./HistoryManagementLanding";
import { ViewInvoiceHistory } from "./ViewInvoiceHistory";
import { ViewJobHistory } from "./ViewJobHistory";

const HistoryManagement = (props) => {
  return (
    <div>
      <Switch>
        <Route
          path="/dashboard/history-management"
          component={HistoryManagementLanding}
          exact
        />
        <Route
          path="/dashboard/history-management/view-invoice-history"
          component={ViewInvoiceHistory}
        />
        <Route
          path="/dashboard/history-management/view-job-history"
          component={ViewJobHistory}
        />
      </Switch>
    </div>
  );
};

export default HistoryManagement;
