import React, { useState, useEffect } from "react";
import "./ViewEmployeeDetails.css";
import { DashboardHeader } from "../..";
import { Table, Switch } from "antd";
import { db } from "../../../../firebase";
import { GoPrimitiveDot } from "react-icons/go";

const ViewEmployeeDetails = (props) => {
  const [rostersList, setRostersList] = useState([]);
  const [jobsList, setJobsList] = useState([]);

  useEffect(() => {
    getRosters();
  }, []);

  const { name, email, employeeID } = props.location.state;

  // console.log("rostersList", rostersList);

  const getRosters = () => {
    db.collection("rosters")
      .where("employeeID", "==", employeeID)
      .onSnapshot((querySnapshot) => {
        let datas = [];
        querySnapshot.forEach((doc) => {
          const { taskName, jobID, isActive } = doc.data();
          const data = { rosterID: doc.id, taskName, jobID, isActive };
          datas = [...datas, data];
        });
        setRostersList(datas);
        getJobs(datas);
      });
  };

  const getJobs = (datas) => {
    // console.log("datas", datas);

    Array.from(new Set(datas.map(({ jobID }) => jobID))).map((jobID) => {
      db.collection("jobs")
        .doc(jobID)
        .get()
        .then((doc) => {
          const { jobTitle, clientName } = doc.data();
          const data = {
            jobID: doc.id,
            jobTitle,
            clientName,
          };
          setJobsList((prevState) => [...prevState, data]);
        });
    });
  };

  // console.log(jobsList);

  // const handleActionChange = async (rosterID, isActive) => {
  //   await rosterID.forEach((id) =>
  //     db.collection("rosters").doc(id).update({
  //       isActive: !isActive,
  //     })
  //   );
  // };

  const datas = () =>
    jobsList.map(({ jobID, jobTitle, clientName }) => ({
      jobTitle,
      subTasks: rostersList
        .filter((roster) => roster.jobID === jobID)
        .map(({ taskName }) => taskName),
      clientName,
      isActive: rostersList
        .filter((roster) => roster.jobID === jobID)
        .map(({ isActive }) => isActive),
      // rosterID: rostersList
      //   .filter((roster) => roster.jobID === jobID)
      //   .map(({ rosterID }) => rosterID),
    }));

  const COLUMNS = [
    {
      title: "Job",
      dataIndex: "jobTitle",
      key: "jobTitle",
    },
    {
      title: "Task",
      dataIndex: "subTasks",
      key: "subTasks",
    },
    {
      title: "Client",
      dataIndex: "clientName",
      key: "clientName",
    },
    {
      title: "Status",
      key: "isActive",
      dataIndex: "isActive",
      width: "5%",
      render: (text, record) => (
        <>
          {record.isActive.includes(true) ? (
            <GoPrimitiveDot style={{ color: "#0f8015" }} />
          ) : (
            <GoPrimitiveDot style={{ color: "burlywood" }} />
          )}
        </>
      ),
    },
    // {
    //   title: "Action",
    //   dataIndex: "action",
    //   key: "action",
    //   width: "5%",
    //   render: (text, record) => {
    //     console.log(record);
    //     return (
    //       <Switch
    //         onChange={() =>
    //           handleActionChange(record.rosterID, record.isActive)
    //         }
    //         defaultChecked={record.isActive.includes(true) && true}
    //       />
    //     );
    //   },
    // },
  ];

  const dataSource = datas().map(
    ({ jobTitle, subTasks, clientName, isActive, rosterID }, index) => ({
      key: index,
      jobTitle,
      clientName,
      isActive,
      // rosterID,
      subTasks: (
        <ul className="viewEmployeeDetails__subTasks">
          {subTasks.map((subTask, index) => (
            <li key={index}>{subTask}</li>
          ))}
        </ul>
      ),
    })
  );

  return (
    <>
      <DashboardHeader header={name} goBack={props.history.goBack} isNested />
      <div className="viewEmployeeDetails">
        <p>
          Name: <span>{name}</span>
        </p>
        <p>
          Email: <span>{email}</span>
        </p>
        <div className="viewEmployeeDetails__table">
          <Table columns={COLUMNS} dataSource={dataSource} />
        </div>
      </div>
    </>
  );
};

export default ViewEmployeeDetails;
