import { Space, Table, Switch } from "antd";
import React, { useEffect, useState } from "react";
import { DashboardHeader } from "../../DashboardHeader";
import { Link } from "react-router-dom";
import "./ManageStaffUsers.css";
import { MdDeleteForever } from "react-icons/md";
import { db } from "../../../../firebase";
import { GoPrimitiveDot } from "react-icons/go";

const ManageStaffUsers = (props) => {
  const [staffData, setstaffData] = useState([]);

  useEffect(() => {
    StaffList();
  }, []);

  const handleActionChange = async (id, isActive) => {
    await db.collection("users").doc(id).update({
      isActive: !isActive,
    });
  };

  const StaffList = () => {
    db.collection("users")
      .where("role", "==", "employee")
      .onSnapshot((querySnapShot) => {
        const items = [];
        querySnapShot.forEach((doc) => {
          const data = { ...doc.data(), id: doc.id };
          items.push(data);
        });
        setstaffData(items);
      });
  };

  const columns = [
    {
      title: "Staff Name",
      dataIndex: "name",
      key: "name",
      render: (text) => <Link>{text}</Link>,
    },

    {
      title: "Phone",
      dataIndex: "contact",
      key: "contact",
      render: (text) => <Link>{text}</Link>,
    },
    {
      title: "Email address",
      dataIndex: "email",
      key: "email",
      render: (text) => <Link>{text}</Link>,
    },
    {
      title: "Status",
      key: "status",
      dataIndex: "status",
      render: (text, record) => (
        <>
          {record.isActive ? (
            <GoPrimitiveDot style={{ color: "#0f8015" }} />
          ) : (
            <GoPrimitiveDot style={{ color: "burlywood" }} />
          )}
        </>
      ),
    },
    {
      title: "Action",
      dataIndex: "action",
      key: "action",
      render: (text, record) => (
        <Switch
          onChange={() => handleActionChange(record.id, record.isActive)}
          defaultChecked={record.isActive && true}
        />
      ),
    },
    {
      title: "",
      dataIndex: "remove",
      key: "remove",
      render: (text, record) => (
        <Space size="middle">
          <Link style={{ fontSize: "1.5rem" }}>
            <MdDeleteForever />
          </Link>
        </Space>
      ),
    },
  ];

  return (
    <>
      <DashboardHeader
        header="Manage Staff Users"
        goBack={props.history.goBack}
        isNested
      />
      <div className="manage_staff_users">
        <h3>Staff Users</h3>
        <Table columns={columns} dataSource={staffData} />
      </div>
    </>
  );
};

export default ManageStaffUsers;
