import React from "react";
import {
  ScheduleLanding,
  NewJob,
  NewSchedule,
  ManageSchedule,
  ScheduleInfo,
} from ".";
import { Switch, Route } from "react-router-dom";

function ScheduleAndRoster() {
  return (
    <>
      <Switch>
        <Route
          path="/dashboard/schedule-and-roster/create-cleaning-schedule"
          component={ScheduleLanding}
          exact
        />
        <Route
          path="/dashboard/schedule-and-roster/create-cleaning-schedule/create-new-job"
          component={NewJob}
        />
        <Route
          path="/dashboard/schedule-and-roster/create-cleaning-schedule/create-new-schedule"
          component={NewSchedule}
        />{" "}
        <Route
          path="/dashboard/schedule-and-roster/create-cleaning-schedule/manage-schedule"
          component={ManageSchedule}
          exact
        />{" "}
        <Route
          path="/dashboard/schedule-and-roster/create-cleaning-schedule/manage-schedule/:id"
          component={ScheduleInfo}
        />
      </Switch>
    </>
  );
}

export default ScheduleAndRoster;
