import React from "react";
import { ScheduleAndRosterLanding, Schedule, Roster } from ".";
import { Switch, Route } from "react-router-dom";

function ScheduleAndRoster() {
  return (
    <>
      <Switch>
        <Route
          path="/dashboard/schedule-and-roster"
          component={ScheduleAndRosterLanding}
          exact
        />
        <Route
          path="/dashboard/schedule-and-roster/create-cleaning-schedule"
          component={Schedule}
        />
        <Route
          path="/dashboard/schedule-and-roster/create-cleaning-roster"
          component={Roster}
        />
      </Switch>
    </>
  );
}

export default ScheduleAndRoster;
