import React, { useState, useEffect } from "react";
import "./Roster.css";
import DashboardHeader from "./../../DashboardHeader/DashboardHeader";
import { Form, Button, Select, Modal } from "antd";
import { db } from "../../../../firebase";

const Roster = (props) => {
  const [clientsList, setClientsList] = useState([]);
  const [jobsList, setJobsList] = useState([]);
  const [schedulesList, setSchedulesList] = useState([]);
  const [employeesList, setEmployeesList] = useState([]);
  const [formValues, setFormValues] = useState({});
  const [isModalVisible, setIsModalVisible] = useState(false);
  const [step, setStep] = useState(1);
  const [isJobDisabled, setIsJobDisabled] = useState(true);
  const [isTaskDisabled, setIsTaskDisabled] = useState(true);

  useEffect(() => {
    getClients();
    getEmployees();
  }, []);

  const [form] = Form.useForm();
  const { Option } = Select;

  const getClients = () => {
    db.collection("users")
      .where("role", "==", "client")
      .get()
      .then((querySnapshot) => {
        let datas = [];
        querySnapshot.forEach((doc) => {
          const data = { ...doc.data(), id: doc.id };
          datas = [...datas, data];
        });
        setClientsList(datas);
      });
  };

  const getEmployees = () => {
    let datas = [];
    db.collection("users")
      .where("role", "==", "employee")
      .get()
      .then((querySnapshot) => {
        querySnapshot.forEach((doc) => {
          const data = {
            employeeID: doc.id,
            name: doc.data().name,
          };
          datas = [...datas, data];
        });
        setEmployeesList(datas);
      });
  };

  const getJobs = (clientID) => {
    let datas = [];
    db.collection("jobs")
      .where("clientID", "==", clientID)
      .get()
      .then((querySnapshot) => {
        querySnapshot.forEach((doc) => {
          const data = { jobID: doc.id, jobTitle: doc.data().jobTitle };
          datas = [...datas, data];
        });
        setJobsList(datas);
      });
  };

  const getSchedules = (jobTitle) => {
    let datas = [];
    db.collection("schedules")
      .where("clientID", "==", formValues.clientID)
      .where("jobTitle", "==", jobTitle)
      .get()
      .then((querySnapshot) => {
        querySnapshot.forEach((doc) => {
          const { taskName, frequency, days, date } = doc.data();
          const data = {
            scheduleID: doc.id,
            taskName,
            frequency,
            days,
            date,
          };
          datas = [...datas, data];
        });
        setSchedulesList(datas);
      });
  };

  const handleAssign = (values) => {
    setFormValues({ ...formValues, ...values });
    setIsModalVisible(true);
  };

  // console.log("formValues", formValues);

  const handleOk = () => {
    const { employeeID, employeeName, jobID } = formValues;

    const scheduleInfo = schedulesList.find(
      (schedule) => schedule.scheduleID === formValues.scheduleID
    );

    db.collection("rosters")
      .doc()
      .set({
        employeeID,
        employeeName,
        jobID,
        ...scheduleInfo,
      });
    setStep(step + 1);
  };

  const handleCancel = () => {
    setIsModalVisible(false);
    setStep(1);
  };

  const handleClient = (clientID) => {
    getJobs(clientID);
    setFormValues({ ...formValues, clientID });
    setIsJobDisabled(false);
    form.setFieldsValue({ jobTitle: null, task: null });
  };

  const handleJob = (jobTitle, jobID) => {
    getSchedules(jobTitle);
    setFormValues({ ...formValues, jobID });
    setIsTaskDisabled(false);
  };

  const switchForm = () => {
    switch (step) {
      case 1: {
        const { jobTitle, taskName, employeeName } = formValues;

        return (
          <div className="rosterClosing">
            <p>
              Job Title: <span>{jobTitle}</span>
            </p>
            <p>
              Task: <span>{taskName}</span>
            </p>
            <p>
              Emplyoee: <span>{employeeName}</span>
            </p>
          </div>
        );
      }
      case 2: {
        return <p>Roster has been created successfully.</p>;
      }
      default:
        break;
    }
  };

  return (
    <>
      <DashboardHeader
        header="Create Cleaning Roster"
        goBack={props.history.goBack}
        isNested
      />
      <div className="roster">
        <Form
          form={form}
          name="dynamic_form_nest_item"
          onFinish={handleAssign}
          autoComplete="off"
        >
          <Form.Item
            label="Client"
            name="client"
            rules={[{ required: true, message: "Required!" }]}
          >
            <Select
              size="large"
              onChange={(value, { key }) => handleClient(key)}
              showSearch
              optionFilterProp="children"
              filterOption={(input, option) =>
                option.children.toLowerCase().indexOf(input.toLowerCase()) >= 0
              }
            >
              {clientsList.map(({ id, name, email }) => (
                <Option key={id} value={name}>
                  <p className="roster__dropOption">
                    <span>{name}</span>
                    <span>{email}</span>
                  </p>
                </Option>
              ))}
            </Select>
          </Form.Item>
          <Form.Item
            name="jobTitle"
            label="Job Title"
            rules={[{ required: true, message: "Required" }]}
          >
            <Select
              size="large"
              disabled={isJobDisabled}
              onChange={(value, { key }) => handleJob(value, key)}
              showSearch
              optionFilterProp="children"
              filterOption={(input, option) =>
                option.children.toLowerCase().indexOf(input.toLowerCase()) >= 0
              }
            >
              {jobsList?.map(({ jobTitle, jobID }) => (
                <Option key={jobID} value={jobTitle}>
                  {jobTitle}
                </Option>
              ))}
            </Select>
          </Form.Item>
          <Form.Item
            name="taskName"
            label="Task Name"
            rules={[{ required: true, message: "Required" }]}
          >
            <Select
              size="large"
              disabled={isTaskDisabled}
              onChange={(value, { key }) =>
                setFormValues({ ...formValues, scheduleID: key })
              }
              showSearch
              optionFilterProp="children"
              filterOption={(input, option) =>
                option.children.toLowerCase().indexOf(input.toLowerCase()) >= 0
              }
            >
              {schedulesList?.map(({ taskName, scheduleID }) => (
                <Option key={scheduleID} value={taskName}>
                  {taskName}
                </Option>
              ))}
            </Select>
          </Form.Item>
          <Form.Item
            name="employeeName"
            label="Employee"
            rules={[{ required: true, message: "Required" }]}
          >
            <Select
              size="large"
              onChange={(value, { key }) =>
                setFormValues({ ...formValues, employeeID: key })
              }
              showSearch
              optionFilterProp="children"
              filterOption={(input, option) =>
                option.children.toLowerCase().indexOf(input.toLowerCase()) >= 0
              }
            >
              {employeesList?.map(({ name, employeeID }) => (
                <Option key={employeeID} value={name}>
                  {name}
                </Option>
              ))}
            </Select>
          </Form.Item>
          <Form.Item>
            <Button type="button" className="bg-dark" htmlType="submit">
              ASSIGN
            </Button>
          </Form.Item>
        </Form>
      </div>
      <Modal
        title="New Roster"
        visible={isModalVisible}
        onOk={handleOk}
        onCancel={handleCancel}
        footer={[
          <Button
            className={step === 1 ? "bg-light" : "btnHide"}
            key="back"
            onClick={handleCancel}
          >
            Cancel
          </Button>,
          <Button
            className={step === 1 ? "bg-dark" : "btnHide"}
            key="ok"
            onClick={handleOk}
          >
            Ok
          </Button>,
        ]}
      >
        {switchForm()}
      </Modal>
    </>
  );
};

export default Roster;
