import React, { useState, useEffect } from "react";
import "./EmployeeBasis.css";
import { DashboardHeader } from "../..";
import { Table, Switch } from "antd";
import { db } from "../../../../firebase";
import { GoPrimitiveDot } from "react-icons/go";
import { Link } from "react-router-dom";

const EmployeeBasis = (props) => {
  const [employeesList, setEmployeesList] = useState([]);

  useEffect(() => {
    getEmployees();
  }, []);

  // console.log(employeesList);

  const handleActionChange = async (employeeID, isActive) => {
    await db.collection("users").doc(employeeID).update({
      isActive: !isActive,
    });
  };

  const getEmployees = () => {
    db.collection("users")
      .where("role", "==", "employee")
      .onSnapshot((querySnapshot) => {
        let datas = [];
        querySnapshot.forEach((doc) => {
          const { name, email, isActive } = doc.data();
          const data = { employeeID: doc.id, name, email, isActive };
          datas = [...datas, data];
        });
        setEmployeesList(datas);
      });
  };

  const COLUMNS = [
    {
      title: "Employee",
      dataIndex: "name",
      key: "name",
      render: (text, record) => (
        <Link
          to={{
            pathname: `/dashboard/manage-jobs/employee-basis/${record.employeeID}`,
            state: record,
          }}
        >
          {text}
        </Link>
      ),
    },
    {
      title: "Status",
      key: "status",
      dataIndex: "status",
      width: "5%",
      render: (text, record) => (
        <>
          {record.isActive ? (
            <GoPrimitiveDot style={{ color: "#0f8015" }} />
          ) : (
            <GoPrimitiveDot style={{ color: "burlywood" }} />
          )}
        </>
      ),
    },
    {
      title: "Action",
      dataIndex: "action",
      key: "action",
      width: "5%",

      render: (text, record) => (
        <Switch
          onChange={() =>
            handleActionChange(record.employeeID, record.isActive)
          }
          defaultChecked={record.isActive && true}
        />
      ),
    },
  ];

  return (
    <>
      <DashboardHeader
        header="On the basis of Employee"
        goBack={props.history.goBack}
        isNested
      />
      <div className="employeeBasis">
        <Table
          dataSource={employeesList?.map((employee) => ({
            ...employee,
            key: employee.employeeID,
          }))}
          columns={COLUMNS}
        />
      </div>
    </>
  );
};

export default EmployeeBasis;
