import React, { useState, useEffect } from "react";
import "./ManageSchedule.css";
import { DashboardHeader } from "../../..";
import { Table, Button } from "antd";
import { db } from "../../../../../firebase";
import { useHistory } from "react-router-dom";

const ManageSchedule = (props) => {
  const [jobsList, setJobsList] = useState([]);

  useEffect(() => {
    getJobs();
  }, []);

  const history = useHistory();

  const getJobs = () => {
    db.collection("jobs")
      .get()
      .then((querSnapshot) => {
        let datas = [];
        querSnapshot.forEach((doc) => {
          const data = { ...doc.data(), jobID: doc.id };
          datas = [...datas, data];
        });
        setJobsList(datas);
      });
  };

  const handleManage = (record) => {
    history.push({
      pathname: `/dashboard/schedule-and-roster/create-cleaning-schedule/manage-schedule/${record.id}`,
      state: record,
    });
  };

  const COLUMNS = [
    {
      title: "Job",
      dataIndex: "jobTitle",
      key: "jobTitle",
      width: "45%",
    },
    {
      title: "Client",
      dataIndex: "clientName",
      key: "clientName",
      width: "45%",
    },
    {
      title: "Action",
      dataIndex: "action",
      key: "action",
      width: "10%",
      render: (text, record) => (
        <Button
          className="bg-dark"
          style={{ padding: "0 2rem" }}
          onClick={() => handleManage(record)}
        >
          MANAGE
        </Button>
      ),
    },
  ];

  return (
    <>
      <DashboardHeader
        header="Manage Schedule"
        goBack={props.history.goBack}
        isNested
      />
      <div className="manageSchedule">
        <Table
          dataSource={jobsList?.map((job) => ({ ...job, key: job.jobID }))}
          columns={COLUMNS}
        />
      </div>
    </>
  );
};

export default ManageSchedule;
