import React, { useState, useEffect } from "react";
import "./NewJob.css";
import { DashboardHeader } from "../../..";
import { Form, Input, DatePicker, Button, Select, Modal } from "antd";
import moment from "moment";
import { db } from "../../../../../firebase";

const NewJob = (props) => {
  const [isModalVisible, setIsModalVisible] = useState(false);
  const [formValues, setFormValues] = useState();
  const [clientsList, setClientsList] = useState([]);
  const [step, setStep] = useState(1);

  useEffect(() => {
    getClients();
  }, []);

  const { Option } = Select;

  const handleOk = () => {
    const { clientID, jobTitle, clientName } = formValues;

    db.collection("jobs")
      .add({
        clientID,
        jobTitle,
        clientName,
        startDate: moment(formValues.startDate).format("LL"),
      })
      .then((docRef) => {
        db.collection("sites").doc().set({
          jobID: docRef.id,
          siteName: jobTitle,
          longitude: "",
          latitude: "",
        });
      })
      .catch((err) => console.error(err));
    setStep(step + 1);
  };

  const handleCancel = () => {
    setIsModalVisible(false);
    setStep(1);
  };

  const handleCreate = (values, key) => {
    setIsModalVisible(true);
    setFormValues({
      ...formValues,
      ...values,
      jobTitle: values.jobTitle
        .toLowerCase()
        .split(" ")
        .map((word) => word.charAt(0).toUpperCase() + word.substring(1))
        .join(" "),
    });
  };

  const getClients = () => {
    db.collection("users")
      .where("role", "==", "client")
      .get()
      .then((querySnapshot) => {
        let datas = [];
        querySnapshot.forEach((doc) => {
          const data = { ...doc.data(), id: doc.id };
          datas = [...datas, data];
        });
        setClientsList(datas);
      });
  };

  const switchForm = () => {
    switch (step) {
      case 1: {
        return (
          <div className="newJobClosing">
            <p>
              Title: <span>{formValues?.jobTitle}</span>
            </p>
            <p>
              Client: <span>{formValues?.clientName}</span>
            </p>
            <p>
              Start Date:{" "}
              <span>{moment(formValues?.startDate).format("LL")}</span>
            </p>
          </div>
        );
      }
      case 2: {
        return <p>Job has been created successfully.</p>;
      }
      default:
        break;
    }
  };

  return (
    <>
      <DashboardHeader
        header="Create New Job"
        goBack={props.history.goBack}
        isNested
      />
      <div className="newJob">
        <Form id="newJobForm" name="create-new-job" onFinish={handleCreate}>
          <Form.Item
            label="Title"
            name="jobTitle"
            rules={[{ required: true, message: "Required!" }]}
          >
            <Input size="large" />
          </Form.Item>
          <Form.Item
            label="Client"
            name="clientName"
            rules={[{ required: true, message: "Required!" }]}
          >
            <Select
              size="large"
              onChange={(value, { key }) => setFormValues({ clientID: key })}
              showSearch
              optionFilterProp="children"
              filterOption={(input, option) =>
                option.children.toLowerCase().indexOf(input.toLowerCase()) >= 0
              }
            >
              {clientsList.map(({ id, name, email }) => (
                <Option key={id} value={name}>
                  <p className="newJob__dropOption">
                    <span>{name}</span>
                    <span>{email}</span>
                  </p>
                </Option>
              ))}
            </Select>
          </Form.Item>
          <Form.Item
            label="Start Date"
            name="startDate"
            rules={[{ required: true, message: "Required!" }]}
          >
            <DatePicker size="large" placeholder="" />
          </Form.Item>
          <Form.Item style={{ marginBottom: 0 }}>
            <div className="newJob__createBtn">
              <Button className="bg-dark" form="newJobForm" htmlType="submit">
                {" "}
                CREATE
              </Button>
            </div>
          </Form.Item>
        </Form>
      </div>
      <Modal
        title="New Job"
        visible={isModalVisible}
        onOk={handleOk}
        onCancel={handleCancel}
        footer={[
          <Button
            className={step === 1 ? "bg-light" : "btnHide"}
            key="back"
            onClick={handleCancel}
          >
            Cancel
          </Button>,
          <Button
            className={step === 1 ? "bg-dark" : "btnHide"}
            key="ok"
            onClick={handleOk}
          >
            Ok
          </Button>,
        ]}
      >
        {switchForm()}
      </Modal>
    </>
  );
};

export default NewJob;
