import React from "react";
import { Route, Switch } from "react-router-dom";
import './AddUser.css';
import {AddUserLanding} from './AddUserLanding';
import {AddClient} from './AddClient';
import {AddStaff} from './AddStaff';

const AddUser = () => {
  return (
    <>
        <div className="add_users">
      <Switch>
        <Route path="/dashboard/add-user" component={AddUserLanding} exact />
        <Route
          path="/dashboard/add-user/add-clients"
          component={AddClient}
        />
        <Route
          path="/dashboard/add-user/add-staff"
          component={AddStaff}
        />
      </Switch>
    </div>
    </>
  );
};

export default AddUser;
