import {ManageUsersLanding} from './ManageUsersLanding';
import {ManageStaffUsers} from './ManageStaffUsers';

const ManageUsers = () => {
    return (
        <>
        <div className="manage_user">
            <Switch>
                <Route path="/dashboard/manage-users" component={ManageUsersLanding} exact/>
                <Route path='/dashboard/manage-users/manage-client-user' component={ManageClinetUsers}/>
                <Route path='/dashboard/manage-users/manage-staff-user' component={ManageStaffUsers}/>
            </Switch>
        </div>

            
        </>
    )
}

export default ManageUsers
