import React, { useState, useEffect } from "react";
import "./ScheduleInfo.css";
import { DashboardHeader } from "../../..";
import { Table, Button, Modal, Form, Select, Input } from "antd";
import { db } from "../../../../../firebase";

const ScheduleInfo = (props) => {
  const [schedulesList, setSchedulesList] = useState([]);
  const [isModalVisible, setIsModalVisible] = useState(false);
  const [isDaysDisabled, setIsDaysDisabled] = useState(false);
  const [isDateDisabled, setIsDateDisabled] = useState(false);
  const [recordID, setRecordID] = useState("");

  useEffect(() => {
    getSchedules();
  }, []);

  // console.log(props);

  const { jobID, jobTitle, clientID } = props.location.state;
  const { Option } = Select;
  const [form] = Form.useForm();

  const getSchedules = () => {
    db.collection("schedules")
      .where("clientID", "==", clientID)
      .where("jobID", "==", jobID)
      .onSnapshot((querySnapshot) => {
        let datas = [];
        querySnapshot.forEach((doc) => {
          const data = { ...doc.data(), scheduleID: doc.id };
          datas = [...datas, data];
        });
        setSchedulesList(datas);
      });
  };

  const handleManage = (record) => {
    const { taskName, frequency, days, date } = record;
    form.setFieldsValue({
      taskName,
      frequency,
      days: days ? days : setIsDaysDisabled(true),
      date: date ? date : setIsDateDisabled(true),
    });
    setIsModalVisible(true);
    setRecordID(record.scheduleID);
    // console.log("record", record);
  };

  const handleCancel = () => {
    setIsModalVisible(false);
    isDaysDisabled && setIsDaysDisabled(false);
    isDateDisabled && setIsDateDisabled(false);
  };

  const handleSave = (values) => {
    // console.log("values", values);
    const { taskName, frequency, days, date } = values;

    const updates = {
      taskName: taskName
        .toLowerCase()
        .split(" ")
        .map((word) => word.charAt(0).toUpperCase() + word.substring(1))
        .join(" "),
      frequency,
      days: isDaysDisabled ? null : days,
      date: isDateDisabled ? null : date,
    };

    db.collection("schedules").doc(recordID).update(updates);

    db.collection("rosters")
      .where("scheduleID", "==", recordID)
      .get()
      .then((querySnapshot) => {
        querySnapshot.forEach((doc) => {
          db.collection("rosters").doc(doc.id).update(updates);
        });
      });

    setIsModalVisible(false);
    isDaysDisabled && setIsDaysDisabled(false);
    isDateDisabled && setIsDateDisabled(false);
  };

  const handleChange = (value) => {
    if (
      value === "Daily" ||
      value === "Business Days" ||
      value === "Weekends"
    ) {
      setIsDaysDisabled(true);
      setIsDateDisabled(true);
    } else if (value === "Monthly") {
      setIsDaysDisabled(true);
      setIsDateDisabled(false);
    } else {
      setIsDaysDisabled(false);
      setIsDateDisabled(true);
    }
  };

  const getDates = () => {
    const options = [];
    for (let i = 1; i < 31; i++) options.push(i);
    return (
      <Select size="large" style={{ width: "90px" }} disabled={isDateDisabled}>
        {options.map((option) => (
          <Option
            key={option}
            value={
              option +
              (option > 0
                ? ["th", "st", "nd", "rd"][
                    (option > 3 && option < 21) || option % 10 > 3
                      ? 0
                      : option % 10
                  ]
                : "")
            }
          >
            {option +
              (option > 0
                ? ["th", "st", "nd", "rd"][
                    (option > 3 && option < 21) || option % 10 > 3
                      ? 0
                      : option % 10
                  ]
                : "")}
          </Option>
        ))}
      </Select>
    );
  };

  const COLUMNS = [
    {
      title: "Task Name",
      dataIndex: "taskName",
      key: "taskName",
      width: "30%",
    },

    {
      title: "Frequency",
      dataIndex: "frequency",
      key: "frequency",
      width: "30%",
    },
    {
      title: "Appointment/s",
      dataIndex: ["days", "date"],
      key: "appointments",
      width: "30%",

      render: (text, record) => (
        <>
          {record.date || record.days
            ? record.date
              ? record["date"]
              : record["days"].join(", ")
            : ""}
        </>
      ),
    },
    {
      title: "Action",
      dataIndex: "action",
      key: "action",
      width: "10%",
      render: (text, record) => (
        <Button
          className="bg-dark"
          style={{ padding: "0 2rem" }}
          onClick={() => handleManage(record)}
        >
          MANAGE
        </Button>
      ),
    },
  ];
  return (
    <>
      <DashboardHeader
        header={jobTitle}
        goBack={props.history.goBack}
        isNested
      />
      <div className="scheduleInfo">
        <Table
          dataSource={schedulesList}
          dataSource={schedulesList?.map((schedule) => ({
            ...schedule,
            key: schedule.scheduleID,
          }))}
          columns={COLUMNS}
        />
        ;
      </div>
      <Modal
        title="Manage Schedule"
        className="scheduleInfoModal"
        visible={isModalVisible}
        onCancel={handleCancel}
        maskClosable={false}
        footer={[
          <Button
            className="bg-dark"
            key="submit"
            htmlType="submit"
            form="manageScheduleForm"
          >
            SAVE CHANGES
          </Button>,
        ]}
      >
        <Form
          className="scheduleInfoModal__form"
          id="manageScheduleForm"
          form={form}
          onFinish={handleSave}
        >
          <Form.Item
            label="Task Name"
            name="taskName"
            rules={[{ required: true, message: "Required!" }]}
          >
            <Input size="large" style={{ fontSize: "14px" }} />
          </Form.Item>

          <Form.Item
            label="Frequency"
            name="frequency"
            rules={[{ required: true, message: "Required!" }]}
          >
            <Select
              size="large"
              style={{ fontSize: "14px", width: "100%" }}
              onChange={(value) => handleChange(value)}
            >
              <Option value="Daily">Daily (All 7 Days)</Option>
              <Option value="Business Days">Business Days (Mon - Fri)</Option>
              <Option value="Weekends">Weekends (Sat - Sun)</Option>
              <Option value="Fortnightly">Fortnightly</Option>
              <Option value="Monthly">Monthly</Option>
              <Option value="Weekly">Weekly</Option>
            </Select>
          </Form.Item>

          <Form.Item
            label="Day/s"
            name="days"
            rules={
              !isDaysDisabled && [{ required: true, message: "Required!" }]
            }
          >
            <Select
              mode="multiple"
              size="large"
              style={{ width: "100%" }}
              disabled={isDaysDisabled}
            >
              <Option value="Sunday">Sunday</Option>
              <Option value="Monday">Monday</Option>
              <Option value="Tuesday">Tuesday</Option>
              <Option value="Wednesday">Wednesday</Option>
              <Option value="Thursday">Thursday</Option>
              <Option value="Friday">Friday</Option>
              <Option value="Saturday">Saturday</Option>
            </Select>
          </Form.Item>

          <Form.Item
            label="Date"
            name="date"
            rules={
              !isDateDisabled && [{ required: true, message: "Required!" }]
            }
          >
            {getDates()}
          </Form.Item>
        </Form>
      </Modal>
    </>
  );
};

export default ScheduleInfo;
