import React from "react";
import { Button, Input, message, Form } from "antd";
import "./AddStaff.css";
import { DashboardHeader } from "../..";
import firebase, { db } from "./../../../../firebase";

const AddStaff = (props) => {
  const [form] = Form.useForm();

  const onFinish = (values) => {
    console.log(values);
    let date = firebase.firestore.Timestamp.now();

    let StaffEmail = {
      uploadedAt: date,
      email: values.email,
    };

    form.resetFields();

    let accountEmail = {
      uploadedAt: date,
      email: values.email,
      status:'active',
    };

    db.collection("Staff Email")
      .doc()
      .set(StaffEmail)
      .then(() => {
        message.success("client email submitted successfully");
      })
      .catch((error) => message.error(error));

      db.collection("Account")
      .doc()
      .set(accountEmail)
      .then(() => {
        // message.success("client email submitted successfully");
      })
      .catch((error) => message.error(error));
  };


  return (
    <>
      <DashboardHeader
        header={"Add Staffs"}
        goBack={props.history.goBack}
        isNested
      />
      <div className="add_staff_container">
        <h3>Email Address*</h3>
        <Form
        form={form}
          name="normal_login"
          className="login-form"
          onFinish={onFinish}
          initialValues={{
            remember: true,
          }}
        >
          <Form.Item
            name="email"
            rules={[
              {
                required: true,
                type: "email",
                message: "Please input your email address!",
              },
            ]}
          >
            <Input/>
          </Form.Item>

          <Form.Item>
            <Button className="bg-dark" htmlType="submit">
              Register Clients
            </Button>
          </Form.Item>
        </Form>
      </div>
    </>
  );
};

export default AddStaff;
