import React from 'react'
import { Route, Switch } from 'react-router-dom'
import ViewInvoiceLanding from './ViewInvoiceLanding/ViewInvoiceLanding';
import {ClientInvoiceHistory} from './ClientInvoiceHistory';
import {StaffInvoiceHistory} from './StaffInvoiceHistory';

const ViewInvoiceHistory = () => {
    return (
        <div>
            <Switch>
                <Route path="/dashboard/history-management/view-invoice-history" component={ViewInvoiceLanding} exact/>
                <Route path="/dashboard/history-management/view-invoice-history/client-invoice-history" component={ClientInvoiceHistory}/>
                <Route path="/dashboard/history-management/view-invoice-history/staff-invoice-history" component={StaffInvoiceHistory}/>
            </Switch>
            
        </div>
    )
}

export default ViewInvoiceHistory
