import { Table, Switch } from "antd";
import React, { useState, useEffect } from "react";
import { DashboardHeader } from "../../DashboardHeader";
import { Link } from "react-router-dom";
import "./ManageClientUsers.css";
import { db } from "../../../../firebase";
// import Checkbox from "antd/lib/checkbox/Checkbox";
import { GoPrimitiveDot } from "react-icons/go";

const ManageClinetUsers = (props) => {
  const [clientData, setClientData] = useState([]);
  // const [status, setstatus] = useState(false);

  useEffect(() => {
    ClientList();
  }, []);

  // const handleStatus = (record) => {
  //   // console.log(record.status);
  //   setstatus(!record.status);
  // };

  const handleActionChange = async (id, isActive) => {
    await db.collection("users").doc(id).update({
      isActive: !isActive,
    });
  };

  // useEffect(() => {
  //   ClientList(isActive);
  // }, [isActive]);

  // const ClientList = (isActive) => {
  const ClientList = () => {
    db.collection("users")
      .where("role", "==", "client")
      // .get()
      .onSnapshot((querySnapShot) => {
        const items = [];
        querySnapShot.forEach((doc) => {
          const data = { ...doc.data(), id: doc.id };

          // let newStatus = {
          //   isActive: false,
          // };
          // const Data = Object.assign(data, newStatus);
          items.push(data);
        });
        // console.log("items", items);
        setClientData(items);
      });
    // .catch((err) => message.error(err));
  };

  const columns = [
    {
      title: "Client Name",
      dataIndex: "name",
      key: "name",
      render: (text) => <Link>{text}</Link>,
    },
    {
      title: "Business Name",
      dataIndex: "businessName",
      key: "businessname",
      render: (text) => <Link>{text}</Link>,
    },
    {
      title: "Phone",
      dataIndex: "phone",
      key: "phone",
      render: (text) => <Link>{text}</Link>,
    },
    {
      title: "Email address",
      dataIndex: "email",
      key: "email",
      render: (text) => <Link>{text}</Link>,
    },
    {
      title: "Business address",
      dataIndex: "businessAddress",
      key: "businessaddress",
      render: (text) => <Link>{text}</Link>,
    },
    {
      title: "Business Type",
      dataIndex: "businessType",
      key: "businesstype",
      render: (text) => <Link>{text}</Link>,
    },
    {
      title: "ABN",
      dataIndex: "australianBusinessNumber",
      key: "abn",
      render: (text) => <Link>{text}</Link>,
    },
    {
      title: "Status",
      dataIndex: "status",
      key: "status",
      render: (text, record, index) => (
        <Link>
          {record.isActive === true ? (
            <GoPrimitiveDot className="active" />
          ) : (
            <GoPrimitiveDot style={{ color: "burlywood" }} />
          )}
        </Link>
      ),
    },
    {
      title: "Action",
      dataIndex: "action",
      key: "action",

      render: (text, record) => (
        <Switch
          onChange={() => handleActionChange(record.id, record.isActive)}
          defaultChecked={record.isActive && true}
        />
      ),
    },

    // {
    //   title: "Action",
    //   dataIndex: "action",
    //   key: "actions",
    //   render: (text, record, index) => (
    //     <Space size="middle">
    //       <Checkbox key={index} onClick={() => handleStatus(record)}>
    //         {status === true ? "Deactivate" : "Activate"}
    //       </Checkbox>
    //     </Space>
    //   ),
    // },
  ];

  return (
    <>
      <DashboardHeader
        header="Manage Client Users"
        goBack={props.history.goBack}
        isNested
      />
      <div className="manage_client_users">
        <h3 style={{ marginBottom: "15px" }}>Client Users</h3>
        <Table columns={columns} dataSource={clientData} />
      </div>
    </>
  );
};

export default ManageClinetUsers;
