import React, { useState } from "react";
import "./Dashboard.css";
import {
  DashboardLanding,
  Sidebar,
  HeaderButton,
  ManageJobs,
  AddUser,
  ManageUsers,
  ScheduleAndRoster,
  HistoryManagement,
} from "../../components/UI";
import { AiOutlineMenu } from "react-icons/all";
import { Switch, Route } from "react-router-dom";

const Dashboard = () => {
  const [isMobMenuVisible, setIsMobMenuVisible] = useState(false);

  const showMobMenu = () => setIsMobMenuVisible(!isMobMenuVisible);

  return (
    <div className="dashboard">
      <div className="dashboard__sidebar">
        <Sidebar
          isMobMenuVisible={isMobMenuVisible}
          showMobMenu={showMobMenu}
        />
      </div>
      <div className="dashboard__content">
        <div className="dashboard__header">
          <span className="dashboard__mobMenuOpen">
            <AiOutlineMenu onClick={showMobMenu} />
          </span>
          <HeaderButton />
        </div>
        <Switch>
          <Route path="/dashboard" component={DashboardLanding} exact />
          <Route path="/dashboard/add-user" component={AddUser} />
          <Route path="/dashboard/manage-users" component={ManageUsers} />
          <Route path="/dashboard/manage-jobs" component={ManageJobs} />
          <Route
            path="/dashboard/schedule-and-roster"
            component={ScheduleAndRoster}
          />
          <Route
            path="/dashboard/history-management"
            component={HistoryManagement}
          />
        </Switch>
      </div>
    </div>
  );
};

export default Dashboard;
