import React, { useState, useEffect } from "react";
import "./JobBasis.css";
import { DashboardHeader } from "../..";
import { useHistory } from "react-router-dom";
import { Button, Table, Switch } from "antd";
import { GoPrimitiveDot } from "react-icons/go";
import { Link } from "react-router-dom";
import { db } from "../../../../firebase";
// import { useData } from "../../../../contexts/dataContexts";

const JobBasis = (props) => {
  const [jobsList, setJobsList] = useState([]);

  // const { getJobs, jobsList } = useData();

  useEffect(() => {
    getJobs();
  }, []);

  // console.log(jobsList);

  const history = useHistory();

  const handleActionChange = async (jobID, isActive) => {
    await db.collection("jobs").doc(jobID).update({
      isActive: !isActive,
    });

    const isActived = await db
      .collection("jobs")
      .doc(jobID)
      .get()
      .then((doc) => doc.data().isActive);

    // console.log(isActived);

    db.collection("schedules")
      .where("jobID", "==", jobID)
      .get()
      .then((querySnapshot) => {
        querySnapshot.forEach((doc) => {
          doc.ref.update({ isActive: isActived });
        });
      });

    db.collection("rosters")
      .where("jobID", "==", jobID)
      .get()
      .then((querySnapshot) => {
        querySnapshot.forEach((doc) => {
          doc.ref.update({ isActive: isActived });
        });
      });
  };

  const getJobs = () => {
    db.collection("jobs").onSnapshot((querySnapshot) => {
      let datas = [];
      querySnapshot.forEach((doc) => {
        const data = { ...doc.data(), jobID: doc.id };
        datas = [...datas, data];
      });
      setJobsList(datas);
    });
  };

  const COLUMNS = [
    {
      title: "Job",
      dataIndex: "jobTitle",
      key: "jobTitle",
      width: "45%",
      render: (text, record) => (
        <Link
          to={{
            pathname: `/dashboard/manage-jobs/job-basis/${record.jobID}`,
            state: record,
          }}
        >
          {text}
        </Link>
      ),
    },
    {
      title: "Client",
      dataIndex: "clientName",
      key: "client",
      width: "45%",
    },
    {
      title: "Status",
      key: "isActive",
      dataIndex: "isActive",
      width: "5%",
      render: (text, record) => (
        <>
          {record.isActive ? (
            <GoPrimitiveDot style={{ color: "#0f8015" }} />
          ) : (
            <GoPrimitiveDot style={{ color: "burlywood" }} />
          )}
        </>
      ),
    },
    {
      title: "Action",
      dataIndex: "action",
      key: "action",
      width: "5%",
      render: (text, record) => (
        <Switch
          onChange={() => handleActionChange(record.jobID, record.isActive)}
          defaultChecked={record.isActive && true}
        />
      ),
    },
  ];

  return (
    <>
      <DashboardHeader
        header="On the basis of Job"
        goBack={props.history.goBack}
        isNested
      />
      <div className="jobBasis">
        <div className="jobBasis__assignBtn">
          <Button
            onClick={() =>
              history.push("/dashboard/manage-jobs/job-basis/assign-staff")
            }
            className="bg-dark"
            style={{ width: "8rem", height: "2.5rem" }}
          >
            ASSIGN STAFF
          </Button>
        </div>
        <div className="jobBasis__table">
          <Table
            dataSource={jobsList?.map((job) => ({
              ...job,
              key: job.jobID,
            }))}
            columns={COLUMNS}
          />
        </div>
      </div>
    </>
  );
};

export default JobBasis;
