import React, { useState } from "react";
import { useHistory } from "react-router";
import { Form, Input, Button, Select, message, Spin } from "antd";
import { UserOutlined, LockOutlined } from "@ant-design/icons";
import "./RegisterForm.css";
import { Link } from "react-router-dom";
import logo from "./../../../assets/logo.png";
import { useAuth } from "../../../contexts/authContexts";

const { Option } = Select;

const RegisterForm = () => {
  const { login } = useAuth();
  const [isLoggingIn, setIsLoggingIn] = useState(false);
  let history = useHistory();

  const onFinish = async (values) => {
    try {
      setIsLoggingIn(true);

      if (
        values.email === "prismasofts.developer@gmail.com" &&
        values.password === "123456"
      ) {
        await login(values.email, values.password);
        history.push("/dashboard");
      } else {
        setIsLoggingIn(false);
        message.error("Invalid User");
      }
    } catch (error) {
      message.error(error);
    }
    setIsLoggingIn(false);
  };

  return (
    <Form
      name="normal_login"
      className="login-form"
      initialValues={{
        remember: true,
      }}
      onFinish={onFinish}
    >
      <Link to="/">
        <img src={logo} alt="Vic Uni Student Record Management" className="logo" />
      </Link>

      <h2>Register</h2>
      <Form.Item
        name="email"
        rules={[
          {
            required: true,
            type: "email",
            message: "Please input your email address!",
          },
        ]}
      >
        <Input
          prefix={<UserOutlined className="site-form-item-icon" />}
          placeholder="Email"
        />
      </Form.Item>
      <Form.Item
        name="password"
        rules={[
          {
            required: true,
            message: "Please input your Password!",
          },
        ]}
      >
        <Input
          prefix={<LockOutlined className="site-form-item-icon" />}
          type="password"
          placeholder="Password"
        />
      </Form.Item>
      <Form.Item>
        <Select placeholder="Select Role">
              <Option value="Student">Student</Option>
              <Option value="Faculty">Faculty</Option>
              <Option value="ThirdParty">Third Party</Option>
            </Select>
      </Form.Item>

      <Form.Item>
        <Button htmlType="submit" className="login-form-button bg-dark">
          {isLoggingIn ? <Spin style={{ color: "white" }} /> : null}
          Register
        </Button>
        <p style={{ textAlign: "center", paddingTop: "10px" }}>
          Already have an account? <Link style={{ textAlign: "center", paddingTop: "10px" }} to="/"> Sign In</Link>
        </p>
      </Form.Item>
    </Form>
  );
};

export default RegisterForm;
