import React, { useState, useEffect } from "react";
import "./ViewDetails.css";
import { DashboardHeader } from "../..";
import { Table, Switch, Button, Modal } from "antd";
import { db } from "../../../../firebase";
import { GoPrimitiveDot } from "react-icons/go";

const ViewDetails = (props) => {
  const [schedulesList, setSchedulesList] = useState([]);
  const [rostersList, setRostersList] = useState([]);
  const [isModalVisible, setIsModalVisible] = useState(false);

  const datas = [props.location.state];
  const { jobTitle, jobID } = props.location.state;

  useEffect(() => {
    getSchedules();
    getRosters();
  }, []);

  const getSchedules = async () => {
    db.collection("schedules")
      .where("jobID", "==", jobID)
      .onSnapshot((querySnapshot) => {
        let datas = [];
        querySnapshot.forEach((doc) => {
          const { taskName, isActive } = doc.data();
          const data = {
            scheduleID: doc.id,
            taskName,
            isActive,
          };
          datas = [...datas, data];
        });
        setSchedulesList(datas);
      });
  };

  const getRosters = () => {
    db.collection("rosters")
      .where("jobID", "==", jobID)
      .onSnapshot((querySnapshot) => {
        let datas = [];
        querySnapshot.forEach((doc) => {
          const { employeeID, employeeName, isActive } = doc.data();
          const data = {
            rosterID: doc.id,
            employeeID,
            employeeName,
            isActive,
          };
          datas = [...datas, data];
        });
        setRostersList(datas);
      });
  };

  const handleTaskAction = async (scheduleID, { isActive }) => {
    await db.collection("schedules").doc(scheduleID).update({
      isActive: !isActive,
    });
  };

  const handleEmployeeAction = async (rosterID, { isActive, employeeID }) => {
    await db.collection("rosters").doc(rosterID).update({
      isActive: !isActive,
    });
  };

  const handleUpdate = () => {
    navigator.geolocation.getCurrentPosition(function (position) {
      db.collection("sites")
        .where("jobID", "==", jobID)
        .get()
        .then((querySnapshot) =>
          querySnapshot.forEach((doc) => {
            doc.ref.update({
              latitude: position.coords.latitude,
              longitude: position.coords.longitude,
            });
          })
        );
    });
    setIsModalVisible(true);
  };

  const handleCancel = () => {
    setIsModalVisible(false);
  };

  const uniqueRosters = Array.from(
    new Set(rostersList.map(({ employeeID }) => employeeID))
  ).map((employeeID) =>
    rostersList.find((roster) => roster.employeeID === employeeID)
  );

  // console.log(schedulesList);

  const TASK_COLUMNS = [
    {
      title: "Job Title",
      dataIndex: "jobTitle",
      key: "job",
      width: "45%",
    },
    {
      title: "Client Name",
      dataIndex: "clientName",
      key: "clientName",
    },
    {
      title: "Starting Date",
      dataIndex: "startDate",
      key: "startDate",
      width: "15%",
    },
  ];

  const SUB_TASK_COLUMNS = [
    {
      title: "Task Name",
      dataIndex: "taskName",
      key: "taskName",
    },
    {
      title: "Status",
      key: "isActive",
      dataIndex: "isActive",
      width: "5%",
      render: (text, record) => (
        <>
          {record.isActive ? (
            <GoPrimitiveDot style={{ color: "#0f8015" }} />
          ) : (
            <GoPrimitiveDot style={{ color: "burlywood" }} />
          )}
        </>
      ),
    },
    {
      title: "Action",
      dataIndex: "action",
      key: "action",
      width: "5%",

      render: (data, record) => (
        <Switch
          onChange={() => handleTaskAction(record.scheduleID, record)}
          defaultChecked={record.isActive && true}
        />
      ),
    },
  ];

  const EMPLOYEE_TABLE_COLUMNS = [
    {
      title: "Employee Name",
      dataIndex: "employeeName",
      key: "employeeName",
    },
    {
      title: "Status",
      key: "isActive",
      dataIndex: "isActive",
      width: "5%",
      render: (text, record) => (
        <>
          {record.isActive ? (
            <GoPrimitiveDot style={{ color: "#0f8015" }} />
          ) : (
            <GoPrimitiveDot style={{ color: "burlywood" }} />
          )}
        </>
      ),
    },
    {
      title: "Action",
      dataIndex: "action",
      key: "action",
      width: "5%",

      render: (data, record) => (
        <Switch
          onChange={() => handleEmployeeAction(record.rosterID, record)}
          defaultChecked={record.isActive && true}
        />
      ),
    },
  ];

  return (
    <>
      <DashboardHeader
        header={jobTitle}
        goBack={props.history.goBack}
        isNested
      />
      <div className="viewDetails">
        <div className="viewDetails__table">
          <Table dataSource={datas} columns={TASK_COLUMNS} pagination={false} />
        </div>
        <div className="viewDetails__updateLocation">
          <Button className="bg-dark" onClick={handleUpdate}>
            UPDATE LOCATION
          </Button>
        </div>
        <div className="viewDetails__table">
          <h3>SUB TASKS</h3>
          <Table
            dataSource={schedulesList?.map((schedule) => ({
              ...schedule,
              key: schedule.scheduleID,
            }))}
            columns={SUB_TASK_COLUMNS}
            pagination={false}
          />
        </div>
        <div className="viewDetails__table">
          <h3>EMPLOYEE TABLE</h3>
          <Table
            dataSource={uniqueRosters.map((roster) => ({
              ...roster,
              key: roster.rosterID,
            }))}
            columns={EMPLOYEE_TABLE_COLUMNS}
            pagination={false}
          />
        </div>
      </div>
      <Modal
        title="Update"
        visible={isModalVisible}
        onCancel={handleCancel}
        footer={[]}
      >
        <p>Location updated successfully.</p>
      </Modal>
    </>
  );
};

export default ViewDetails;
